/** @jsx h */
import { FunctionComponent, h } from "preact"
import './Button.less';

interface ButtonProps {
  onClick: () => void;
  text: string;
  classes?: string
}

const Button: FunctionComponent<ButtonProps> = ({ onClick, text , classes}) => {

  return (
    <button className={`buttonPrimary ${classes}`} onClick={ onClick }>
      { text }
    </button>
  )
};

export default Button;